@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Yellowtail&display=swap);
.note-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    align-self: stretch;
}
.content-input {
    height: 500px;
    padding: 0;
    text-align: left;
    width: 100%;
    margin-bottom: 1em;
}
.input-title {
    width: 100%;
}
.cancel-btn {
    margin-right: 1em;
}
/* Note card styles */
.notes-container {
    align-self: stretch;
}
.note {
    display: flex;
    flex-direction: column;
    width: 100%;
    /* border: 2px solid black; */
    font-size: 1em;
    padding: 0.5em;
}
.content {
    white-space: pre-wrap;
    margin: 0.5em 0 0.5em 0;
}
.title {
    font-size: 0.75em;
    text-decoration: underline;
}
.date-posted {
    font-size: 0.6em;
}
.note-btns{
    align-self: flex-end;
}
.note-btns > *{
    padding: 2px;
}
.edit-btn{
    margin-right: 0.5em;
}
/* Note card styles */
* {
    box-sizing: border-box;
    margin:0;
    padding:0;
    font-family: 'Open Sans', sans-serif;
}
.container {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    top: 0;
    bottom: 50px;
    position: fixed;
    overflow-y: auto;
    background-color: rgb(235, 235, 193);
}
.note-border{
    border-bottom: 0.5px solid rgba(128,0,0,0.5);
    margin-right: 0.5em;
    margin-left: 0.5em;
}
.footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    border-top: 1px solid black;
    background-color: aqua;
}
button {
    padding: 2px;
}
.menu-btn {
    margin-left: 2em;
}
.create-btn {
    margin-right: 2em;
}
.logo {
    font-family: 'Yellowtail', cursive;
    margin-right: 5px;
    margin-left: 5px;
}
